import { Injectable } from '@angular/core';
import { DataSourceItem, ReactiveDataSource } from '@matheo/datasource';
import { PagedResultDtoOfUserDto, UserDto } from '@shared/service-proxies/service-proxies';
import { of } from 'rxjs';
import { UsuariosDatabase } from './usuarios.database';
import { UsuariosGetAllRequest } from './usuarios.interfaces';

@Injectable()
export class UsuariosDatasource extends ReactiveDataSource<UsuariosGetAllRequest, PagedResultDtoOfUserDto, UserDto> {
  constructor(protected database: UsuariosDatabase) {
    super();

    this.config = {
      debug: false,
      autoStart: true // filter BehaviorSubject
    };
    this.pageSize = 10;
  }

  rawDefault() {
    return {
      items: []
    } as PagedResultDtoOfUserDto;
  }

  rawFetch(args: UsuariosGetAllRequest) {
    return this.database.getAll(args);
  }

  rawTotal(result: PagedResultDtoOfUserDto) {
    return of(result.items.length);
  }

  rawResult(result: PagedResultDtoOfUserDto): UserDto[] {
    return result.items;
  }

  filter(query: string, limit: number): void {
    throw new Error('Not Implemented');
  }

  resFilter(result: UserDto[]): DataSourceItem[] {
    throw new Error('Not Implemented');
  }
}
