import { SelectionModel } from '@angular/cdk/collections';
import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSlideToggleChange } from '@angular/material/slide-toggle';
import { UsuariosDatasource } from '@app/usuarios/services';
import { getterPaginator } from '@matheo/datasource';
import { UserDto } from '@shared/service-proxies/service-proxies';

@Component({
  selector: 'app-usuarios-list-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss']
})
export class UsuariosListTableComponent implements OnInit {
  @Input() source: UsuariosDatasource;
  @Input() columns = ['select', 'name', 'contact', 'role', 'actions'];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  selection: SelectionModel<UserDto>;

  constructor() { }

  ngOnInit() {
    this.source.setPaginator(this.paginator, getterPaginator(this.paginator));

    const initialSelection = [];
    const allowMultiSelect = true;
    this.selection = new SelectionModel<UserDto>(allowMultiSelect, initialSelection);
  }

  editUser(user: UserDto) {
    // TODO update user
  }

  toggleActive(event: MatSlideToggleChange, id: number) {
    // TODO implement the API call
    console.log(event.checked ? 'Activate' : 'Deactivate', id)
  }

  batchDelete() {
    // TODO use the selection.selected
  }

  /**
   * Selection Support
   */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.source.data.length;
    return numSelected === numRows;
  }

  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.source.data.forEach(row => this.selection.select(row));
  }
}
