import { Injectable } from '@angular/core';
import { UserServiceProxy } from '@shared/service-proxies/service-proxies';
import { UsuariosGetAllRequest } from './usuarios.interfaces';

@Injectable({
  providedIn: 'root'
})
export class UsuariosDatabase {
  constructor(private users: UserServiceProxy) {}

  get(id?: number | null) {
    return this.users.get(id);
  }

  getAll(args: UsuariosGetAllRequest) {
    return this.users.getAll(args.query, args.active, args.pageIndex, args.pageSize);
  }
}
