import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { catchError, take } from 'rxjs/operators';
import { UsuariosDatabase } from './usuarios.database';
import { UserDto } from '@shared/service-proxies/service-proxies';

@Injectable({
  providedIn: 'root'
})
export class UsuariosResolver implements Resolve<UserDto> {
  constructor(private router: Router, private database: UsuariosDatabase) {}

  resolve(route: ActivatedRouteSnapshot): Observable<UserDto> {
    const id = Number(route.paramMap.get('id'));

    return this.database.get(id).pipe(
      take(1),
      catchError(() => {
        this.router.navigate(['/app/usuarios']);
        return of({} as UserDto);
      })
    );
  }
}
